@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header .navbar-brand {
  font-size: 1rem;
}

.Header .brand {
  width: 200px;
}

.Header .banner {
  width: 100vw;
}

.Rsvp {
  background-color: #152346;
  color: white;
}

.Rsvp .header {
  font-size: 5vmin;
  width: 50%;
  margin: 0 auto;
  padding-top: 25px;
}

.Rsvp .highlight {
  background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, rgba(255, 255, 255, 0)), color-stop(50%, #FF00AA));
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 50%, #FF00AA 50%);
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 50%, #FF00AA 50%);
  padding: 0 5px;
  font-weight: bold;
}

.Rsvp iframe {
  padding-top: 25px;
}

@media (max-width: 992px) {
  .Rsvp .header {
    width: 90%;
  }
}

.App {
  font-family: 'IBM Plex Mono', monospace;
  text-align: center;
}

